$green: #8DBFAC;
$monserat-medium: "Montserrat Medium", sans-serif;
$monserat-bold: "Monserrat Bold", sans-serif;

.main-block-lost {
  font-family: $monserat-medium;
  color: $green;

  .lost-left-side {
    margin-bottom: 35px;

    .tags-for-search {
      margin-top: 26px;
      height: 30px;

      input {
        height: 30px;
        border: 1px solid $green;
        border-radius: 15px;
        font-size: 12px;
        padding-left: 10px;
        margin-right: 12px;
      }

      input:nth-of-type(1) {
        margin-left: 10px;
      }

      input:nth-of-type(1),
      input:nth-of-type(2) {
        width: 145px;
      }

      input:nth-of-type(3) {
        width: 337px;
      }

      input:nth-of-type(4) {
        width: 226px;
      }

      .map-view-btn,
      .list-view-btn {
        justify-content: space-around;
        justify-self: right;
        border-radius: 17px;
        background-color: $green;
        font-size: 12px;
        color: white;

        .fa-map-marked,
        .fa-list {
          margin-right: 10px;
        }
      }
    }

    .map-container {
      margin-top: 10px;
      margin-bottom: -35px;
      padding-left: 6px;
      background-color: $green;
      border-top-left-radius: 15px;
      font-size: 12px;
      color: white;

      span {
        margin-left: 14px;
        margin-top: 5px;
        cursor: pointer;

        i {
          margin-left: -6px;
          margin-right: 10px;
        }
      }

      .fa-map-marker {
        font-size: 32px;
        color: #FFE18B;
        text-shadow: -1px 1px 0 #414141, 1px 1px 0 #414141, 1px -1px 0 #414141, -1px -1px 0 #414141;

        .fa-paw {
          position: absolute;
          top: 5px;
          left: 3.5px;
          font-size: 16px;
          text-shadow: none;
          color: #393939;
        }
      }
    }
  }

  .lost-right-side {
    background-color: $green;
  }
}

@media screen and (min-width: 1280px){
  .main-block-lost {
    .lost-right-side {
      display: none;
    }
  }
}

@media screen and (max-width: 959px){
  .main-block-lost {
    .lost-right-side {
      display: none;
    }
  }
}

@media screen and (min-width: 1280px){
  .main-block-lost {
    .lost-left-side {
      .map-view-btn,
      .list-view-btn {
        display: none;
      }
    }
  }

}

@media screen and (max-width: 1279px){
  .main-block-lost {
    .lost-left-side {
      .map-container {
        display: none;
      }

      .tags-for-search {
        height: fit-content;
        margin-bottom: 10px;

        input:nth-of-type(3) {
          width: 250px;
          margin-bottom: 10px;
        }
      }
    }
  }
}

@media screen and (max-width: 600px){
  .main-block-lost {
    .lost-left-side {
      .tags-for-search {
        text-align: center;

        input:nth-of-type(1),
        input:nth-of-type(2),
        input:nth-of-type(3),
        input:nth-of-type(4),
        .map-view-btn,
        .list-view-btn {
          width: 90%;
          height: 40px;
          margin-top: 20px;
        }

        input:nth-of-type(3) {
          margin-bottom: 0;
        }

        .map-view-btn,
        .list-view-btn {
          justify-content: center;
        }
      }
    }
  }
}