$green: #8DBFAC;
$monserat-bold: "Monserrat Bold", sans-serif;
$monserat-medium: "Montserrat Medium", sans-serif;

.profile-main-block {
  font-size: 16px;
  color: $green;

  .profile-left-side {
    margin-bottom: 35px;

    .header-profile {
      width: calc(100% - 20px);
      margin-top: 30px;
      margin-left: 20px;
      margin-bottom: 0;
      font-family: $monserat-bold;
      font-weight: 700;
    }

    .profile-info-container {
      width: 94.722222%;
      height: fit-content;
      margin-left: 2.083333%;
      margin-right: 10px;
      margin-top: 38px;
      box-shadow: 0 3px 6px #00000029;
      font-family: $monserat-medium;

      .header-profile-buttons {
        height: 34px;
        font-family: $monserat-bold;
        color: white;

        span {
          display: inline-block;
          width: 50%;
          padding: 7px;
          background-color: $green;
          text-align: center;
          cursor: pointer;
        }

        .selected {
          opacity: 0.5;
        }
      }

      .deactivated-text {
        margin-top: 21px;
        padding-top: 11px;
        padding-bottom: 8px;
        text-align: center;
        font-size: 12px;
        font-family: $monserat-medium;
        background-color: $green;
        color: white;

        p {
          margin: 0;
        }
      }

      .profile-info {
        .profile-info-picture {
          margin-top: 27px;
          margin-left: 13px;

          img {
            width: 35px;
            height: 35px;
            border: 1px solid white;
            border-radius: 50%;
          }

          p {
            display: inline-block;
            margin-left: 40px;
            margin-bottom: 0;
          }

          input {
            margin-left: 5px;
            border: none;
            border-bottom: 1px solid $green;
          }

          .fa-pencil-alt {
            margin-left: 11px;
            cursor: pointer;
          }
        }

        fieldset {
          margin-top: 35px;
          margin-bottom: 61px;
          font-family: $monserat-medium;
          font-size: 12px;
          color: #393939;

          .profile-labels {
            box-sizing: border-box;
            float: left;
            width: 20%;

            label {
              display: block;
              text-align: right;
              margin-bottom: 3px;
              margin-right: 17px;
              padding-top: 3px;
            }
          }

          .profile-inputs {
            box-sizing: border-box;
            float: right;
            width: 80%;

            input {
              display: block;
              margin-bottom: 3px;
              border: none;
              border-bottom: 1px solid $green;
            }
          }
        }
      }
    }

    .profile-buttons {
      text-align: right;

      .profile-btn-cancel,
      .profile-submit-btn {
        margin-top: 32px;
        height: 34px;
        font-size: 12px;
        border-radius: 17px;
      }

      .profile-btn-cancel {
        width: 105px;
        background-color: transparent;
        border: 1px solid $green;
        color: $green;
      }

      .profile-submit-btn {
        margin-left: 11px;
        margin-right: 28px;
        width: 148px;
        justify-content: space-between;
        background-color: $green;
        color: white;
      }
    }
  }

  .profile-right-side {
    background-color: $green;
  }
}

@media screen and (max-width: 959px){
  .profile-main-block {
    .profile-right-side {
      display: none;
    }
  }
}